<template>
  <div class="accordion" role="tablist">
    <b-card v-for="(func, index) in abi.filter(x => isReadFunction(x))" :key="index" :title="func.name" no-body
            class="mt-3 border-bottom">
      <b-card-header class="p-0" header-tag="header" role="tab">
        <b-link v-b-toggle="`c${func.name}_${index}`" class="btn-block p-2 border-bottom">{{ index + 1 }}. {{ func.name }}</b-link>
        <b-collapse :id="`c${func.name}_${index}`">
          <b-card-body class="bg-white">
            <b-form-group v-if="func.stateMutability === 'payable'" :label="func.name" :label-for="`payable_${index}`">
              <b-form-input :id="`payable_${index}`"  v-model="formData[`payable_${index}`]" placeholder="payableAmount" />
            </b-form-group>
            <b-form-group v-for="(item, inx) in func.inputs" :key="inx">
              <label :for="`input_${index}_${inx}`">{{ `${item.name} (${item.type})` }}&nbsp;</label>
              <b-link title="Use connected address" v-if="item.type === 'address'"
                      @click="useMyAddress(`input_${index}_${inx}`)">☼</b-link>
              <b-form-input :id="`input_${index}_${inx}`" v-model="formData[`input_${index}_${inx}`]" :placeholder="item.type"/>
            </b-form-group>
            <b-button @click="executeFunction(func, index)" class="bg-primary">Query</b-button>
            <div class="mt-3 mb-0 readContractResult" :id="`result_${index}`"></div>
          </b-card-body>
        </b-collapse>
      </b-card-header>
    </b-card>
  </div>
</template>

<script>
import { myUtil } from '@/utils'

export default {
  name: 'ReadContract',
  props: ['abi', 'address'],
  data: function () {
    return {
      formData: {}
    }
  },
  methods: {
    isReadFunction: function (item) {
      return item.type === 'function' &&
        (item.stateMutability === 'view' || item.stateMutability === 'pure' || item.constant === true)
    },
    getInputs: function (func, index) {
      const allInputs = []
      for (let i = 0; i < func.inputs.length; i++) {
        allInputs.push({ value: this.formData[`input_${index}_${i}`], type: func.inputs[i].type, grp: null })
      }
      return allInputs
    },
    executeFunction: function (func, index) {
      const pre = document.getElementById(`result_${index}`)
      pre.innerHTML = '<span role="status" class="spinner-grow text-success"></span>'
      try {
        const inputs = this.getInputs(func, index)
        const params = myUtil.encodeParams(inputs)
        const txt = `window.currentContract.methods.${func.name}(${params})`
        // eslint-disable-next-line no-eval
        const contractFunction = eval(txt)
        const self = this
        contractFunction.call({ from: '0x0000000000000000000000000000000000000000' }, function (err, res) {
          if (err) {
            pre.innerHTML = `<span class="text-danger">${err.toString()}</span>`
          } else {
            self.showResult(func.name, pre, res, func.outputs)
          }
        })
      } catch (err) {
        pre.innerHTML = `<span class="text-danger">${err.toString()}</span>`
      }
    },
    showResult: function (name, element, result, outputFields) {
      element.innerHTML = '[&nbsp;<b>' + name + '</b> method Response ]<br>' +
        this.formatMultipleOutputs(result, outputFields)
    },
    formatMultipleOutputs: function (strVal, outputFields) {
      let strAnswer = ''
      if (outputFields.length > 1) {
        if (Object.keys(strVal).length > 0) {
          for (let i = 0; i < outputFields.length; i++) {
            strAnswer = strAnswer + '&nbsp;'
            if (outputFields[i] != null && outputFields[i].toString() !== '') {
              strAnswer = strAnswer + ' <strong>' + outputFields[i].name +
                "</strong>&nbsp;<span class='text-monospace text-secondary'><i>" +
                outputFields[i].type + '</i></span><b>:</b>&nbsp;'
            }
            if (strVal[i] === null) {
              strAnswer = strAnswer + this.formatResult('', outputFields[i].type) + '<br>'
            } else {
              strAnswer = strAnswer + this.formatResult(strVal[i].toString(), outputFields[i].type) +
                '<br>'
            }
          }
        }
      } else {
        strAnswer = strAnswer + '&nbsp;'
        strAnswer = strAnswer + ' <strong>' + outputFields[0].name +
          "</strong>&nbsp;<span class='text-secondary'><i>" + outputFields[0].type +
          '</i></span><b>:</b>&nbsp;'
        strVal = this.formatResult(strVal, outputFields[0].type)
        strAnswer = strAnswer + strVal + '<br>'
      }
      return strAnswer
    },
    formatResult: function (strResult, resultType) {
      if (resultType.startsWith('uint')) {
        return new Intl.NumberFormat().format(strResult)
      } else if (resultType === 'string') {
        return strResult
      } else if (resultType === 'address') {
        if (strResult !== '0x0000000000000000000000000000000000000000') {
          return `<a href="${myUtil.supportedNetworks[window.myChainId].scan}/address/${strResult}" target="_blank">${strResult}</a>`
        } else {
          return strResult
        }
      } else {
        return strResult
      }
    },
    replaceAll: function (str, find, replace) {
      return str.toString().replace(new RegExp(find, 'g'), replace)
    },
    useMyAddress: function (id) {
      this.formData[id] = window.myAddress
      document.getElementById(id).value = window.myAddress
    },
    clearFormData: function () {
      this.formData = {}
      document.querySelectorAll('.readContractResult').forEach(function (item) { item.innerHTML = '' })
    }
  }
}
</script>

