<template>
  <b-card class="mb-3">
    <h1>{{ currentContract.name }}</h1>
    <b-form-select v-model="currentContract" class="mb-3">
      <b-form-select-option v-for="contract in contracts" :key="contract.name" v-bind:value="contract">
        {{ contract.name }}
      </b-form-select-option>
    </b-form-select>
    <b-button @click="handleLoadContract" class="bg-info">Load</b-button>
    <b-button @click="$bvModal.show('modal-delete')" class="ml-3 bg-danger">Delete</b-button>
    <b-modal id="modal-delete" title="Confirm" @ok="handleDeleteContract">
      Are you sure to delete this contract?
    </b-modal>
    <b-button @click="$bvModal.show('modal-add')" class="ml-3 bg-primary">New</b-button>
    <b-modal id="modal-add" title="Add new contract" @ok="handleAddNewContract">
      <b-form-group label="Name" label-for="name">
        <b-form-input id="name" v-model="formName" @focus="$event.target.select()"/>
      </b-form-group>
      <b-form-group label="Address" label-for="address">
        <b-form-input id="address" v-model="formAddress" @focus="$event.target.select()"/>
      </b-form-group>
      <b-form-group label="ABI" label-for="abi">
        <b-form-textarea id="abi" v-model="formAbi" @focus="$event.target.select()"/>
      </b-form-group>
      <b-form-group label="URL" label-for="url">
        <b-form-input id="url" v-model="formUrl" @focus="$event.target.select()"/>
      </b-form-group>
    </b-modal>
  </b-card>
</template>

<script>
import { apiGet } from '@/api'

export default {
  name: 'ContractManage',
  data: function () {
    return {
      currentContract: {},
      contracts: [],
      formName: '',
      formAddress: '',
      formUrl: '',
      formAbi: ''
    }
  },
  methods: {
    handleAddNewContract: async function () {
      let contract = null
      for (let i = 0; i < this.contracts.length; i++) {
        if (this.contracts[i].address === this.formAddress) {
          contract = this.contracts[i]
          break
        }
      }
      if (contract == null) {
        contract = {}
        this.contracts.push(contract)
      }
      contract.name = this.formName
      contract.address = this.formAddress
      if (this.formAbi.trim().length > 0) {
        localStorage.setItem(`tco.abi.${this.formAddress}`, this.formAbi)
      } else {
        localStorage.setItem(`tco.abi.${this.formAddress}`, (await apiGet(this.formUrl)).result)
      }
      localStorage.contracts = JSON.stringify(this.contracts)
      this.reloadContracts()
    },
    handleDeleteContract: function () {
      let contract = null
      for (let i = 0; i < this.contracts.length; i++) {
        if (this.contracts[i].address === this.currentContract.address) {
          contract = this.contracts[i]
          this.contracts.splice(i, 1)
          this.currentContract = {}
          break
        }
      }
      if (contract != null) {
        localStorage.removeItem(`tco.abi.${this.formAddress}`)
        localStorage.contracts = JSON.stringify(this.contracts)
        this.reloadContracts()
      }
    },
    handleLoadContract: function () {
      this.$emit('onLoadContract', this.currentContract)
      this.loadFormData()
    },
    loadFormData: function () {
      this.formName = this.currentContract.name
      this.formAddress = this.currentContract.address
      this.formAbi = localStorage.getItem(`tco.abi.${this.formAddress}`)
    },
    reloadContracts: function () {
      if (localStorage.contracts) {
        this.contracts = JSON.parse(localStorage.contracts)
      }
      if (this.contracts.length > 0) {
        this.currentContract = this.contracts[0]
        this.loadFormData()
      }
    }
  },
  mounted () {
    this.reloadContracts()
  }
}
</script>

