<template>
  <b-container class="mt--5 position-sticky">
    <contract-manage @onLoadContract="handleLoadContract"/>
    <b-card v-show="!!contractAddress" class="mb-3">
      <b-link class="btn btn-info btn-block" target="_blank" :href="contractLink" v-if="!!contractAddress">
        {{ contractAddress.substr(0, 20) + '...' + contractAddress.substr(38, 4) }}
      </b-link>
    </b-card>
    <b-tabs v-show="!!contractAddress">
      <b-tab title="Read"><read-contract ref="readContract" v-bind:abi="abi" v-bind:address="contractAddress" /></b-tab>
      <b-tab title="Write"><write-contract ref="writeContract" v-bind:abi="abi" v-bind:address="contractAddress"/></b-tab>
    </b-tabs>
  </b-container>
</template>

<script>
import ReadContract from '@/components/Contract/ReadContract'
import WriteContract from '@/components/Contract/WriteContract'
import ContractManage from '@/components/Contract/ContractManage'
import { myUtil } from '@/utils'
export default {
  name: 'Contract',
  components: { ContractManage, WriteContract, ReadContract },
  methods: {
    handleLoadContract: function (contract) {
      this.$refs.readContract.clearFormData()
      this.$refs.writeContract.clearFormData()
      if (contract == null) {
        this.abi = []
        this.contractAddress = ''
        return
      }
      this.abi = JSON.parse(localStorage.getItem(`tco.abi.${contract.address}`))
      this.contractAddress = contract.address
      window.currentContract = new myUtil.web3.eth.Contract(this.abi, this.contractAddress)
    }
  },
  data () {
    return {
      abi: [],
      contractAddress: ''
    }
  },
  computed: {
    contractLink () {
      return `${myUtil.supportedNetworks[window.myChainId].scan}/address/${this.contractAddress}`
    }
  }
}
</script>
